function Results({ vehicles, onVehicle }) {
    return <>
        <div className="container">
            <ul>
                {
                    vehicles.map(vehicle =>
                        <li key={vehicle.id} onClick={() => {
                            debugger
                            onVehicle()
                        }}>
                            <h1> {vehicle.name} </h1>
                            <img src={vehicle.thumbnail} />
                            <p> {vehicle.price} </p>
                        </li>
                    )
                }
            </ul>
        </div>
    </>
}

export default Results